import { ViewModel, Store } from './view-model';
import { View, Emitter } from './view';

function subscribe() {
  
  // VM,view
  Store.subscribe(function(){  
    var newStatus = Store.getState(); // VM
    Emitter.emit('viewEmitterFromC', newStatus); // View
    console.log('Storestate',Store.getState());
  });

  // ViewdispatchVM
  Emitter.on('viewEmitterToC', function(data){
    console.log("view", data)
    Store.dispatch(data);
  });
}

// Menu
function Menu(config) {
	this.config = config;
  this.init();
}

Menu.prototype = {
  
  init: function() {
    //controllerVM&View

    subscribe(); // 1.VM,view & VIEWVM

    View.init(); // 2.VIEW,controller

    ViewModel.init({
      modelData:this.config.data,
      url: this.config.url
    });
    // 3.VM
    // (1)VMstore
    // (2)VMcontrolleractionVMController
  }
}

export default Menu;



